import React, { Fragment } from "react";
import webDevExpertise from "../../styles/assets/images/Webblog/webdev1.jpg";
import webDevTimeManage from "../../styles/assets/images/Webblog/webdev2.jpg";
import webDevImprovement from "../../styles/assets/images/Webblog/webdev3.jpg";
import { Carousel } from "react-bootstrap";

const WebDevelopment = () => {
  return (
    <Fragment>
      <div className="business-wrap my-5 pt-5">
        <div className="container-fluid">
          <div className="business-need p-2  row">
            <div className="col-12">
              <div className="mx-5">
                <h3 className="mb-2 fw-bold">
                  Why Your Business Needs A Professional Web Development Team
                </h3>
                <p className="text-dark fw-semibold fs-3 mt-4">
                  In today's digital age, having a strong online presence is
                  crucial for any business. A well-designed and user-friendly
                  website can help to attract and retain customers, and can even
                  be a key driver of revenue. However, building and maintaining
                  a website is no easy task. That's where a professional web
                  development team comes in.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <main>
        <div className="container merncarousel">
          <div className="business pb-5">
            <div className="text-center justify-content-center d-flex row">
              <div className="col-md-10">
                <h4 className="fw-semibold text-dark my-5">
                  Why should you invest in a professional web development team
                  for your business? Here are just a few reasons
                </h4>
              </div>
            </div>
          </div>
          <div className="d-flex align-items-center row">
            <div className="col-md-12 pb-5">
              <Carousel>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="fs-5 fw-semibold p-5">
                        <p className="fs-4 fw-semibold">
                          Web development is a complex field that requires a
                          wide range of skills, from coding to design to user
                          experience. A professional web development team will
                          have the expertise and experience necessary to build a
                          high-quality website that meets your business needs.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={webDevExpertise}
                        alt="expertise-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="mern-content">
                        <p className="fs-4 fw-semibold">
                          Building and maintaining a website can be
                          time-consuming,especially if you don't have the
                          technical skills or knowledge. By outsourcing web
                          development to a professional team, you can save time
                          and focus on running your business.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={webDevTimeManage}
                        alt="time-management-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="mern-content">
                        <p className="fs-4 fw-semibold">
                          A professional web development team can design and
                          build a website that is user-friendly, easy to
                          navigate, and visually appealing. This can help to
                          improve the user experience and encourage visitors to
                          stay on your site longer, increasing the chances of
                          conversion.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={webDevImprovement}
                        alt="web-development-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
              </Carousel>
            </div>
          </div>
        </div>
      </main>
    </Fragment>
  );
};

export default WebDevelopment;
