import Layout from "../components/layout";
import Seo from "../components/seo";
import WebDevelopmentScreen from "../screens/website-development";
import React from "react";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import MoveForward from "../components/common/move-forward";
import BreadCrumb from "../components/common/breadcrumb";

const WebDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Web Development Company", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <WebDevelopmentScreen />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Web Development Services"
    headTitle="Web Development Company"
    description="Transforming ideas into digital experiences. Justtry Technologies is your partner in web development. We craft responsive, user-centric websites to elevate your online presence. Explore innovation with us.
    "
    keywords="Web Development Services, Custom Web Development, Responsive Web Design, Front-End Development, Back-End Development, Full-Stack Development, Website Design and Development, E-commerce Website Development, Content Management System (CMS) Development, PHP Web Development, JavaScript Development, HTML5/CSS3 Development, Mobile-Optimized Web Development, Cross-Browser Compatibility, User Interface (UI) Design, User Experience (UX) Design, Progressive Web Apps (PWAs), Web Application Development, Database Integration, API Development and Integration, Website Maintenance and Support, Web Security Services, Performance Optimization, Cloud-Based Web Development, Custom Web Solutions
    "
  ></Seo>
);

export default WebDevelopment;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
